<template>
<div v-loading.fullscreen="loading" class="content content-on-tab">

    <div class="header">
        <span class="el-icon-back back" @click="goBack"> </span>
        <div class="header-title"> Farming Record </div>
        <span class="back" @click="goBack"> </span>
    </div>

    <div class="title">
        <img src="../assets/wallet.svg" />
        <span>Account</span>
    </div>
    <div class="balance-content">
        <div>
            <div class="icon-content">
                <span class="gold-font">I am</span>
            </div>
            <p class="gold-font">{{ role[level] }} </p>
        </div>
        <div>
            <div class="icon-content">
                <span class="gold-font">Balance</span>
            </div>
            <p class="gold-font">{{ balance }} </p>
        </div>
    </div>

    <div class="card"  v-bind:key="index" v-for="(order, index) in orders">
        <div class="box_top"><span>Stake Date: {{order.date }}</span>  <el-tag :type=order.tagtype > {{order.wd }} </el-tag> </div>
        <div class="box_top"><span>Farming Period: {{order.period }} days</span>  </div>
        <div class="card-divider"></div>
        <div class="box_content"><span>Stake Quality：<b>{{ order.value }} BNB</b> </span><span><b>{{ role[order.level] }}</b> </span></div>
        <div class="box_content"><span>Earned：<b>{{ order.percent }} %</b> </span><span>Harvest:<b>{{order.earn }}BNB</b> </span></div>
        
    </div>

    <p class="tips-font ">Tips:<br />
        1. Role depending on the staked BNB. <br />
        2. The higher role, harvest will longer and earn more. <br />
    </p>

</div>
</template>

<script>
/* eslint-disable */
import MyVid from "./../my_vid.js";
import formatNum from "./../numcomp";
import {
    MessageBox
} from 'element-ui';

export default {
    name: 'Order',
    data() {
        return {
            role: {1: "Temporary", 2: "Professional", 3: "Landlord" , 4: "Farmer"},
            level: null,
            balance: null,
            levelCount: null,
            loading: true,
            orders: [],
            orderNums: null,
        }
    },

    created() {

        MyVid.initWeb3Account((web3, acc) => {
            console.log("account:" + acc)
            this.account = acc
            this.web3 = web3
            this.init()

        });
    },
    methods: {

        fn(num) {
            return formatNum(num, 3)
        },

        goBack() {
            this.$router.back(-1)
        },

        async init() {
            this.fov = await MyVid.getFov()
            console.log(this.fov.address);

            this.loading = false;

            this.info()
            this.myOrders()

        },

        info() {
            this.fov.investInfo(this.account, {
                from: this.account
            }).then(
                r => {
                    this.level = r[0].toNumber();
                    this.levelCount = this.web3.utils.fromWei(r[1].toString())
                }
            )

            this.web3.eth.getBalance(this.account).then(r => {
                this.balance = this.fn(this.web3.utils.fromWei(r), 2)
            })
        },

        myOrders() {
            this.orders = []
            this.fov.orderNum({
                from: this.account
            }).then(r => {
                this.orderNums = r.toNumber()
                if (this.orderNums > 0) {
                    this.getOrder(this.orderNums - 1)
                }
                this.$forceUpdate();
            })
        },

        getOrder(index) {
            this.fov.order(index, {
                from: this.account
            }).then(r => {
                let eth = parseFloat(this.web3.utils.fromWei(r[0].toString()))
                let joinTs = r[1] * 1000;
                let withdrawed = r[2];
                let joinTime = new Date(joinTs)
                let dateStr = joinTime.toLocaleDateString().replace(/\//g, "-") + " " + joinTime.toTimeString().substr(0, 8);
                let level = 0;
                let statusStr = "Farming"
                let type = "warning"
                let percent = 3
                let earn = 0
                let period
                let currTime = new Date().getTime();

                if (eth <= 0.2) {
                    level = 1;
                    percent = 5
                    period = 3;
                } else if (eth <= 0.5) {
                    level = 2;
                    percent = 7;
                    period = 7;
                }  else if (eth <= 2) {
                    level = 3;
                    percent = 13;
                    period = 15;
                } else {
                    level = 4;
                    percent = 25;
                    period = 30;
                }

                earn = eth + (eth * percent / 100)
                earn = formatNum(earn, 3);
                let joinEnd = joinTs + period * 1000 * 60 * 10; // 60 * 24;
                if (currTime > joinEnd) {
                    statusStr = "Harvest"
                    type = "info"
                }

                if (withdrawed) {
                    statusStr = "Claimed"
                    type = "warning"
                }

                let order = {
                    value: eth,
                    date: dateStr,
                    wd: statusStr,
                    level: level,
                    percent: percent,
                    period: period,
                    earn: earn,
                    tagtype: type
                }

                this.orders.push(order);

                if (index > 0) {
                    this.getOrder(index - 1)
                }
                this.$forceUpdate();
            });
        }

    }
}
</script>

