<template>
<div v-if="isown" class="content content-on-tab">

    <h2>管理员后台</h2>

    <div>合约当前总资金: <b> {{ totalBalance }} BNB </b></div>

    <div style="text-align: left;">当前管理员： {{ owner }}</div>
    <div class="row-center">
        <el-input placeholder="请输新管理员地址" v-model="newOwner"></el-input>
        <el-button @click="setMgr"> 转移管理员 </el-button>
    </div>

    <div class="row-center">
        <el-input placeholder="提取数量" v-model="wdValue"></el-input>
        <el-button @click="wdFund"> 割一把(提取收益) </el-button>
    </div>

    <div>
        起始推广链接: <el-link type="primary" :href="uLink"> {{ uLink }} </el-link>
    </div>

<div style="text-align: left;">
      <p class="tips-font ">温馨提示：<br />
        1. 转移管理员前，应该先把管理员收益提出。 <br />
        2. 当合约资金不足时，会导致用户提取资金失败。 <br />
        3. 管理员提取收益，会优先扣除管理员应得收益。
        <br />
    </p>
</div>


</div>
</template>

<script>
/* eslint-disable */
// import formatNum from "./../numcomp";
import MyVid from "./../my_vid.js";

export default {
    name: 'Admin',
    data() {
        return {
            owner: null,
            account: null,
            uLink: null,
            fund: 0,
            isown: null,
            totalBalance: null,
            wdValue: null,
            newOwner: null,
        }
    },
    created() {
        MyVid.initWeb3Account((web3, acc) => {
            this.account = acc
            this.web3 = web3
            this.init();
        });
    },
    methods: {

        async init() {
            this.fov = await MyVid.getFov()
            this.info()
        },

        info() {
            this.uLink = process.env.VUE_APP_HOST + 'p/' + this.fov.address

            this.web3.eth.getBalance(this.fov.address).then(r => {
              this.totalBalance = this.web3.utils.fromWei(r)
            })


            this.fov.owner({
                from: this.account
            }).then(r => {
                this.owner = r
                if (this.owner.toLowerCase() == this.account.toLowerCase()) {
                    this.isown = true
                }
                this.balance()
            })

        },

        balance() {
        },

        setMgr() {
            this.fov.transferOwnership(this.newOwner, {
                from: this.account
            }).then(r => {
                this.info()
            })
        },

        // 提取项目 收入
        wdFund() {
            this.fov.wdFee(
                this.web3.utils.toWei(this.wdValue), {
                    from: this.account
                }).then(
                r => {
                    this.info()
                }
            )
        }

    }
}
</script>

