<template>
<div v-loading.fullscreen="loading" class="content">

    <div class="pbox">
        <img class="pagopa_icon" src="../assets/bnb-logo.webp" />
        <div class="pagopa-info">
            <p class="pagopa-label">I'm {{ role[level] }}</p>

            <p v-if="levelCount > 0" class="rate-label">Valid Farm:{{ levelCount }} BNB</p>
            <p v-else class="rate-label">Make your BNB</p>
        </div>

        <div class="raise-info">

            <b class="balance-big">{{ withdrawEarned }}</b>
            <div>
                <span class="eth-label">Total Harvest</span>
            </div>
        </div>
    </div>


    <div class="open-info package">
      <div></div>
      <div>
        <label v-for="(item, index) in levels" :key="index" :class="{'change':item.isCheck}">
          <input
              name="package"
              type="radio"
              v-model="chooseValue"
              :value="item.value"
              :checked="item.isChecked"
              @change="joinChange(index)"
            />
          <b>{{item.label}}</b> </br> {{  role[item.level] }}
        </label>
      </div>
    </div>

    <div class="jbox">
        <el-button class="join" size="big" type="info" round @click="join">
            Stake
        </el-button>
    </div>

    <div class="title">
        <img src="../assets/b_icon.svg" />
        <span>Harvest Record</span>
        <el-link size="small" type="primary" href="#/order">more> </el-link>
    </div>

    <div class="card-bkg mine-box ">
        <div class="award-des">
            <span> Claimable </span>
            <b> {{ canWdEth }} BNB </b>
        </div>
        <el-button size="small" type="primary" @click="withdraw">Claim</el-button>
    </div>

    <div class="title">
        <img src="../assets/pool.svg" />
        <span>Team Work</span>
    </div>

    <div class="card-bkg mine-box">
        <div class="award-des">
            <span> Partners </span>
            <b> {{ parnerNum }} </b>
        </div>
        <el-link type="primary" href="#/node">Details > </el-link>
    </div>

    <div class="card-bkg qrcode-box" >
        <p class="qrcode-title">Your Referral Link</p>
        <p type="primary" class="copylinktext" :href="uLink"> &nbsp; {{ uLink }} </p>
        <div @click="copyUlink">
        <qrcode :value="uLink" :options="{margin: 1, width: 120 }"></qrcode>
        </div>
        
    </div>

</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";
import MyVid from "./../my_vid.js";
import {
    Message,
    MessageBox,
} from 'element-ui';

export default {
    name: 'Fov',
    data() {
        return {
          levels: [
            { level: 1, label: "0.2 BNB", value:"0.2" ,  isCheck: false },
            { level: 2, label: "0.5 BNB", value:"0.5" , isCheck: true },
            { level: 3, label: "2 BNB", value:"2" , isCheck: false },
            { level: 4, label: "5 BNB", value:"5" , isCheck: false },
          ],
            role: {1: "Temporary", 2: "Professional", 3: "Landlord" , 4: "Farmer"},
            web3: null,
            fov: null,
            chooseValue: "2",
            level: null,
            zhUrl: null,
            levelCount: null,
            withdrawEarned: null,
            parnerNum: 0,
            canWdEth: 0,
            uLink: null,
            loading: false,
        }
    },

    created() {
        let addr = this.$route.params.p;

        if (addr && addr.length === 42) {
            this.parentAddr = addr;
            this.zhUrl = "#/p/" + this.parentAddr
        } else {
            console.log("no parent");
        }

        MyVid.initWeb3Account((web3, acc) => {
            console.log("my account address:" + acc)
            this.account = acc
            this.uLink = process.env.VUE_APP_HOST + 'p/' + this.account
            this.web3 = web3
            this.init()

        });
    },
    methods: {

        fn(num) {
            return formatNum(num, 2)
        },

        fnd(num, d) {
            return formatNum(num, d)
        },

        async init() {
            this.fov = await MyVid.getFov()
            console.log(this.fov.address);
            this.loading = false;

            this.info()
            this.getParner()
        },

        info() {
            this.fov.investInfo(this.account, {
                from: this.account
            }).then(
                r => {
                    this.level = r[0].toNumber();
                    this.levelCount = this.web3.utils.fromWei(r[1].toString())
                }
            )

            this.fov.userEarnInfo(this.account, {
                from: this.account
            }).then(
                r => {
                    this.withdrawEarned = this.fn(this.web3.utils.fromWei(r.withdrawEarned.toString()), 4)
                    this.canWdEth = this.fnd(this.web3.utils.fromWei(r.withdrawAble.toString()), 4);
                }
            )

        },

        joinChange(index) {
          this.levels.map((v, i) => {
            if (i == index) {
              v.isCheck = true;
            } else {
              v.isCheck = false;
            }
          });
        },

        getParner() {
            this.fov.allChildNum(this.account, {
                from: this.account
            }).then(r => {
                this.parnerNum = r.toNumber();
                console.log(this.parnerNum)
            })

        },

        copyUlink() {
            if (!navigator.clipboard) {
                var textArea = document.createElement("textarea");
                textArea.value = this.uLink;
                textArea.style.position = "fixed"; //avoid scrolling to bottom
                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();

                try {
                    var successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    console.log('Fallback: Copying text command was ' + msg);

                    Message.info({
                        "message": "URL copyed"
                    })

                } catch (err) {
                }

                document.body.removeChild(textArea);
            } else {
                navigator.clipboard.writeText(this.uLink).then(function () {
                    Message.info({
                        "message": "Promote link copyed"
                    })
                }, function (err) {
                    Message.info({
                        "message": "Promote link copy failed"
                    })
                });
            }

        },

        withdraw() {
            this.fov.withdraw({
                from: this.account
            }).then(
                r => {
                    this.info()
                }
            )
        },

        join() {

            if (!this.parentAddr) {
                MessageBox.alert("Invalid join Partners! ", "Tips", {
                    confirmButtonText: 'OK'
                })

                return;
            } else {
              console.log("chooseValue:" + this.chooseValue);
                this.fov.join(this.parentAddr, {
                    from: this.account,
                    value: this.web3.utils.toWei(this.chooseValue, 'ether')
                }).then(r => {
                    this.info()
                })
            }
        },

    }
}
</script>

